// src/components/header.tsx
'use client';

import Link from 'next/link';
import { Ticket, UserCircle, LogOut, Loader2, Briefcase, ChevronDown, LayoutDashboard, LogInIcon as LogInLucideIcon, UserCog, Settings, Info, FileText, HelpCircle, CreditCard } from 'lucide-react'; // Added CreditCard
import { useAuth } from '@/contexts/AuthContext';
import { Button } from './ui/button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"; 

export default function Header() {
  const { 
    isAdminLoggedIn, adminLogout, adminUser,
    isOrganizerLoggedIn, organizerLogout, organizerUser,
    isLoading 
  } = useAuth();

  const loggedInUser = isAdminLoggedIn ? adminUser : (isOrganizerLoggedIn ? organizerUser : null);
  const loggedInUserType = isAdminLoggedIn ? 'admin' : (isOrganizerLoggedIn ? 'organizer' : null);

  const handleLogout = () => {
    if (isAdminLoggedIn) adminLogout();
    if (isOrganizerLoggedIn) organizerLogout();
  };

  return (
    <header className="bg-primary text-primary-foreground shadow-md sticky top-0 z-50"> {/* Added sticky top-0 z-50 */}
      <div className="container mx-auto px-4 py-3 flex items-center justify-between"> {/* Adjusted padding and justify-between */}
        
        {/* Left Section: Logo */}
        <div className="flex-shrink-0"> {/* Adjusted for better flex behavior */}
          <Link href="/" className="flex items-center gap-2 text-2xl font-bold hover:text-accent transition-colors">
            <Ticket className="h-8 w-8" />
            <span>TrinkaPass</span>
          </Link>
        </div>

        {/* Middle Section: Navigation Links */}
        {/* Hidden on small screens, shown on medium and up */}
        <nav className="hidden md:flex items-center space-x-2 lg:space-x-3">
          <Button variant="link" asChild className="text-primary-foreground hover:text-accent px-2 py-1 lg:px-3 text-sm font-medium">
            <Link href="/">Eventos</Link>
          </Button>
          <Button variant="link" asChild className="text-primary-foreground hover:text-accent px-2 py-1 lg:px-3 text-sm font-medium">
            <Link href="/about">Sobre</Link>
          </Button>
          <Button variant="link" asChild className="text-primary-foreground hover:text-accent px-2 py-1 lg:px-3 text-sm font-medium">
            <Link href="/plans">Planos</Link>
          </Button>
          <Button variant="link" asChild className="text-primary-foreground hover:text-accent px-2 py-1 lg:px-3 text-sm font-medium">
            <Link href="/learn-more">Saber Mais</Link>
          </Button>
        </nav>
        
        {/* Right Section: User Actions / Login */}
        <div className="flex items-center space-x-2 md:space-x-3"> {/* Ensured this is always visible */}
          {isLoading ? (
            <div className="flex items-center gap-2 text-sm p-1 md:p-2">
              <Loader2 size={20} className="animate-spin" />
              <span className="hidden md:inline">Carregando...</span>
            </div>
          ) : loggedInUser && loggedInUserType ? (
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" className="hover:bg-primary/80 hover:text-accent px-2 md:px-3 py-1 h-auto text-sm flex items-center gap-2">
                  {loggedInUserType === 'admin' ? <Briefcase size={18} /> : <UserCircle size={18} />}
                  <span className="font-medium hidden sm:inline">{loggedInUser.nome.split(' ')[0]}</span>
                  <ChevronDown size={16} className="opacity-80"/>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-56 bg-card text-card-foreground mt-2">
                <DropdownMenuLabel className="font-normal text-sm text-muted-foreground">
                  Logado como <span className="font-semibold text-foreground">{loggedInUserType === 'admin' ? 'Admin' : 'Organizador'}</span>
                </DropdownMenuLabel>
                <DropdownMenuSeparator />
                {/* Mobile Nav Links - shown only in dropdown on small screens */}
                <div className="md:hidden">
                  <DropdownMenuItem asChild><Link href="/" className="flex items-center gap-2.5 cursor-pointer w-full"><Ticket size={16} className="text-muted-foreground"/> Eventos</Link></DropdownMenuItem>
                  <DropdownMenuItem asChild><Link href="/about" className="flex items-center gap-2.5 cursor-pointer w-full"><Info size={16} className="text-muted-foreground"/> Sobre</Link></DropdownMenuItem>
                  <DropdownMenuItem asChild><Link href="/plans" className="flex items-center gap-2.5 cursor-pointer w-full"><FileText size={16} className="text-muted-foreground"/> Planos</Link></DropdownMenuItem>
                  <DropdownMenuItem asChild><Link href="/learn-more" className="flex items-center gap-2.5 cursor-pointer w-full"><HelpCircle size={16} className="text-muted-foreground"/> Saber Mais</Link></DropdownMenuItem>
                  <DropdownMenuSeparator/>
                </div>
                {/* End Mobile Nav Links */}
                <DropdownMenuItem asChild>
                  <Link href={loggedInUserType === 'admin' ? "/admin/dashboard" : "/organizer/dashboard"} className="flex items-center gap-2.5 cursor-pointer w-full">
                    <LayoutDashboard size={16} className="text-muted-foreground" /> Painel
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuItem asChild>
                  <Link href={loggedInUserType === 'admin' ? "/admin/profile" : "/organizer/profile"} className="flex items-center gap-2.5 cursor-pointer w-full">
                    <UserCog size={16} className="text-muted-foreground" /> Meu Perfil
                  </Link>
                </DropdownMenuItem>
                {loggedInUserType === 'organizer' && (
                  <DropdownMenuItem asChild>
                    <Link href="/organizer/settings/withdrawal" className="flex items-center gap-2.5 cursor-pointer w-full">
                      <CreditCard size={16} className="text-muted-foreground" /> Saque e Verificação
                    </Link>
                  </DropdownMenuItem>
                )}
                {loggedInUserType === 'admin' && (
                  <DropdownMenuItem asChild>
                    <Link href="/admin/settings" className="flex items-center gap-2.5 cursor-pointer w-full">
                      <Settings size={16} className="text-muted-foreground" /> Configurações
                    </Link>
                  </DropdownMenuItem>
                )}
                <DropdownMenuSeparator />
                <DropdownMenuItem onClick={handleLogout} className="flex items-center gap-2.5 cursor-pointer text-destructive focus:bg-destructive/10 focus:text-destructive w-full">
                  <LogOut size={16} /> Sair
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          ) : (
            <>
              <Button variant="ghost" asChild className="text-primary-foreground hover:text-accent hover:bg-primary/90 p-1 md:px-3 md:py-2 text-sm font-medium">
                  <Link href="/login" className="flex items-center gap-1.5">
                      <LogInLucideIcon size={18} /> Login
                  </Link>
              </Button>
               {/* Mobile Nav Trigger (Hamburger like) when not logged in */}
                <div className="md:hidden">
                    <DropdownMenu>
                        <DropdownMenuTrigger asChild>
                            <Button variant="ghost" size="icon" className="text-primary-foreground hover:text-accent hover:bg-primary/90">
                                <ChevronDown size={20} /> 
                                <span className="sr-only">Menu</span>
                            </Button>
                        </DropdownMenuTrigger>
                        <DropdownMenuContent align="end" className="w-56 bg-card text-card-foreground mt-2">
                             <DropdownMenuItem asChild><Link href="/" className="flex items-center gap-2.5 cursor-pointer w-full"><Ticket size={16} className="text-muted-foreground"/> Eventos</Link></DropdownMenuItem>
                             <DropdownMenuItem asChild><Link href="/about" className="flex items-center gap-2.5 cursor-pointer w-full"><Info size={16} className="text-muted-foreground"/> Sobre</Link></DropdownMenuItem>
                             <DropdownMenuItem asChild><Link href="/plans" className="flex items-center gap-2.5 cursor-pointer w-full"><FileText size={16} className="text-muted-foreground"/> Planos</Link></DropdownMenuItem>
                             <DropdownMenuItem asChild><Link href="/learn-more" className="flex items-center gap-2.5 cursor-pointer w-full"><HelpCircle size={16} className="text-muted-foreground"/> Saber Mais</Link></DropdownMenuItem>
                        </DropdownMenuContent>
                    </DropdownMenu>
                </div>
            </>
          )}
        </div>
      </div>
    </header>
  );
}
